.result{font-family:Test Söhne,sans-serif}.result__wrapper{display:flex;flex-direction:column;align-items:center;padding:49px 65px 49px 66px}.result__image-container{padding-bottom:35px}.result__image{display:block;min-height:529px;box-shadow:0 0 10px rgba(95,187,151,.5019607843);border-radius:50px;object-fit:cover}.result__buttons{display:flex;flex-direction:column;align-items:center;gap:20px}.result__button{padding:15px 92px 14px;background-color:#0c9;box-shadow:0 0 20px rgba(8,15,15,.7490196078);border-radius:50px;font-size:27px;line-height:36px;color:#fff7fc}.result__button--more{color:#080708;background-color:#fceff9;box-shadow:0 0 20px rgba(8,15,15,.5019607843);padding-inline:36px}